import {  createApp } from 'vue'
import {createRouter, createWebHistory } from 'vue-router'

import App from './App.vue'
import HeroBanner from './components/HeroBanner.vue'
import AboutPage from './components/AboutPage.vue'
import CarSuvTyres from './components/CarSuvTyres.vue'
import TruckTyres from './components/TruckTyres.vue'
import TractorTyres from './components/TractorTyres.vue'
import CarTyre from './components/CarTyre.vue'


const router = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', component: HeroBanner  },
        { path: '/car-suv-tyres', component: CarSuvTyres},
        { path: '/truck-tyres', component: TruckTyres},
        { path: '/tractor-tyres', component: TractorTyres},
        { path: '/about', component: AboutPage},
        { path: '/car-tyre', component: CarTyre},
        
    ]
})

import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap"



const app = createApp(App)

app.use(router)

app.mount('#app')
